import { jsPDF } from "jspdf";
var font =
  "";

var font_b =
  "";

var callAddFontN = function () {
  this.addFileToVFS("Aller_Rg-normal.ttf", font);
  this.addFont("Aller_Rg-normal.ttf", "Aller", "normal");
};
var callAddFontB = function () {
  this.addFileToVFS("Aller_Rg-normal.ttf", font_b);
  this.addFont("Aller_Rg-normal.ttf", "Aller", "bold");
};
jsPDF.API.events.push(["addFonts", callAddFontN]);
jsPDF.API.events.push(["addFonts", callAddFontB]);
